import React from "react"
import { Helmet } from "react-helmet"
import classNames from "classnames";
import PropTypes from "prop-types";

import { useStaticQuery, graphql } from "gatsby"

// @material-ui/icons

import "fontsource-roboto"
//@FontAwsome
import { library } from '@fortawesome/fontawesome-svg-core'
import { fab } from '@fortawesome/free-brands-svg-icons'
// import { faCheckSquare, faCoffee } from '@fortawesome/free-solid-svg-icons'

// core components
import LayoutSectionBody from "@components/layout/section/common/body.js";
import PageHeader from "@components/layout/header/header.js";
import PageHeaderLinks from "@components/layout/header/headerLinks.js";
import PageFooter from "@components/layout/footer/footer.js";
import Parallax from "@components/Parallax/Parallax.js";

import { SnackbarProvider } from 'notistack';

import SEO from "@components/seo/seo"

library.add(fab);
const dashboardRoutes = [];



Layout.propTypes = {
  children: PropTypes.node.isRequired,
  pageNode: PropTypes.object.isRequired,
  props: PropTypes.object,
}

//GTag Code
// (function(w,d,s,l,i){w[l]=w[l]||[];w[l].push({'gtm.start':
// new Date().getTime(),event:'gtm.js'});var f=d.getElementsByTagName(s)[0],
// j=d.createElement(s),dl=l!='dataLayer'?'&l='+l:'';j.async=true;j.src=
// 'https://www.googletagmanager.com/gtm.js?id='+i+dl;f.parentNode.insertBefore(j,f);
// })(window,document,'script','dataLayer','GTM-WF2NJHR');

export default function Layout({ children, pageNode, classes, classesPage, classesPost, location,  ...props }) {
  const { site } = useStaticQuery(
    graphql`
      query {
        site {
          siteMetadata {
            defaultThemeName
            componentKeys {
              google {
                analytics
              }
            }
          }
        }
      }
    `
  )

  const { ...rest } = props;

  const themeVariation = (typeof window !== 'undefined') ? (localStorage.getItem('themeVariation') ? localStorage.getItem('themeVariation') : "dark") : "dark";  
  if (typeof window !== 'undefined'){
    localStorage.setItem("themeVariation", themeVariation);
  }

  const bodyClassStyles = [classesPage.page, themeVariation];

  return (
    <SnackbarProvider
      anchorOrigin={{
        vertical: "bottom",
        horizontal: "left",
      }}
    >
      <Helmet
        htmlAttributes={{
          class: classNames([classesPage.htmlRoot, themeVariation]),
        }}
      >
        <script
          async
          src={`https://www.googletagmanager.com/gtag/js?id=${site.siteMetadata.componentKeys.google.analytics}`}
        />
        <script>
          {`
          window.dataLayer = window.dataLayer || [];
          function gtag(){dataLayer.push(arguments);}
          gtag('js', new Date());
        
          gtag('config', '${site.siteMetadata.componentKeys.google.analytics}', { 'anonymize_ip': true });
          `}
        </script>
        <script
          data-ad-client="ca-pub-7263102980773082"
          async
          src="https://pagead2.googlesyndication.com/pagead/js/adsbygoogle.js"
        />
        <body className={classNames(bodyClassStyles)} />
      </Helmet>
      <SEO pageNode={pageNode} />

      <section className="header">
        <PageHeader
          location={location}
          classes={classes}
          classesPage={classesPage}
          color="transparent"
          routes={dashboardRoutes}
          brand="3 Mystic Apes LLC"
          rightLinks={
            <PageHeaderLinks
              location={location}
              pageNode={pageNode}
              classes={classes}
              classesPage={classesPage}
            />
          }
          fixed
          changeColorOnScroll={{
            height: 15,
            color: "dark",
          }}
          {...rest}
        />
        <Parallax
          filter
          image={require("assets/image/background/space.jpg")}
          pageClass={classesPage["parallaxPage"]}
        ></Parallax>
      </section>

      <LayoutSectionBody
        classes={classes}
        classesPage={classesPage}
        classesPost={classesPost}
        pageNode={pageNode}
        location={location}
      >
        <noscript>
          You currently have JavaScript disabled. In order make use of all the
          sites features, JavaScript is required.
        </noscript>
        {children}
      </LayoutSectionBody>

      <section className="footer">
        <PageFooter />
      </section>
    </SnackbarProvider>
  )
}